import React, { Component } from 'react';
import FormGenerator from './FormGenerator';
import { CREATEUSER } from '../../graphql/mutation/remote';

class CreateUser extends Component {
    render(){
        const defaults = {
            username: {
                type: "text",
                default: "",
            },
            password: {
                type: "password",
                default: "",
            }
        };
        return (
            <FormGenerator defaults={defaults} mutation={CREATEUSER} title="Create a new manager" {...this.props}></FormGenerator>
        )
    }
};

export default CreateUser;



